.main-header {
  position:relative;
  display: flex;
  flex-direction: column;
  min-height: 445px;
  background-color:rgba(255,255,255,1);
}

.main-header__logo {
  position:absolute;
  text-align: center;
  z-index:1;
  width: 100%;
}

.main-header__promo {
  position: absolute;
  width: 100%;
  height: 389px;
  background-image: url("../img/promo-image.png");
  background-repeat: no-repeat;
  z-index: 2;
  margin-top:56px;
}

.main-header__promo-text {
  position:absolute;
  background-image:url("../../img/promo-sedona-text.svg");
  width:282px ;
  height: 214px;
  background-repeat:no-repeat;
  top:50%;
  left:50%;
  transform:translate(-50%, -50%);
  z-index: 4;
  text-align: center;
}

.main-header__promo-text-catalog {
  position:absolute;
  background-image:url("../../img/promo-sedona-text-catalog.svg");
  width:229px ;
  height: 39px;
  background-repeat:no-repeat;
  top:50%;
  left:50%;
  transform:translate(-50%, -50%);
  z-index: 4;
  text-align: center;
}

