.product {
  padding:0;
  margin:0;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 1);
  padding: 37px 0 0 0;
}

.product-header {
  padding: 0;
  margin: 0;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
}

.product-header__description {
  padding: 0;
  margin: 0;
  text-transform: uppercase;
  color:rgba(51, 51, 51, 1);
  padding: 50px 20px 0 20px;
  text-align: center;
}

.product-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  padding: 35px 0 10px 0;
}

.product__item {
  position: relative;
  margin: 37px 0 0 0 ;
}

.product__item::before {
  content: " ";
  position: absolute;
  background-color: rgba(229, 229, 229, 1);
  width: 100%;
  height: 1px;
  top:-24px;
 }

 .product__item:last-child::after {
  content: " ";
  position: absolute;
  background-color: rgba(229, 229, 229, 1);
  width: 100%;
  height: 1px;
  bottom:-10px;
 }

 .product__image {
  display: block;
  padding: 0;
  margin: 0 auto;

 }

.product__title {
  margin: 0;
  padding: 0;
  font-weight: 700;
  text-transform: uppercase;
  padding: 13px 0 0 20px;
}

.product__description {
  padding:0;
  margin:0;
  padding: 9px 0 0 20px;
  color:rgba(51, 51, 51, 1);
}

.product__stat-number {
  font-weight: 700;
  padding: 12px 0 0 20px;
  text-transform: uppercase;
}

.product__video-title {
  display: block;
  padding: 0;
  margin: 0;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
  padding:27px 0 0 0;
}

.product__video-description{
  display: block;
  padding: 0;
  margin: 0;
  text-transform: uppercase;
  padding: 40px 0 0 0;
  text-align: center;
}

.product__video-image {
  display: block;
  width: 100%;
  justify-content: center;
  padding: 40px 0 0 0;
}
